@import url(https://fonts.googleapis.com/css2?family=Baloo+Tammudu+2&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.rootDiv {
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction:column;
            flex-direction:column;
    -webkit-align-items:center;
            align-items:center;
    -webkit-justify-content:flex-start;
            justify-content:flex-start;
    background-size:cover;
    padding:50px;
    height:100%;
}

.horizontalDiv {
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction:row;
            flex-direction:row;
    width:100%;
    -webkit-align-items: center;
            align-items: center;
}

.getAppDiv {
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction:row;
            flex-direction:row;
    -webkit-align-items:center;
            align-items:center;
    width:350px;
}

.headerButton {
    width:150px;
    height:90px;
    color:white;
    background-color:transparent;
    border-color:transparent;
    font-size:20pt;
    font-family: 'Baloo Tammudu 2';
    border-radius:5px
}

.button {
    padding:10px;
    width:350px;
    height:70px;
    font-size:20pt;
    border-radius:10px;
    font-family: 'Baloo Tammudu 2';
    border-color:transparent;
}

.appButton {
    width:50px;
    height:50px;
    color:white;
    background-color:transparent;
    border-color:transparent;
    font-size:20pt;
}

.headerText {
    color:white;
    text-align:center;
    font-size:100pt;
    text-shadow: 2px 2px 2px black;
}

.text {
    font-family: 'Baloo Tammudu 2';
    line-height: 1em;
}

.container {
    position: relative;
    margin: 30px;
  }
  
.box {
    height:100%;
    position: absolute;
}
  
.overlay {
    height:100%;
    margin-left:50%;
    margin-top:50%;
}

.stackedImage {
    border-radius: 20px;
    border: 20px solid white;
    width: 400px;
    height: 540px;
    object-fit:none;
}

.partner {
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
    opacity: 0.5;
    height: 60px;    
}
